<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <div slot="header" class="clearfix">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input placeholder="" v-model="unauditedQueryInfo.keywords" clearable @clear="audit('1')" v-if="activeName === 'first'">
              <el-button slot="append" icon="el-icon-search" @click="audit('1')"></el-button>
            </el-input>
            <el-input placeholder="" v-model="auditQueryInfo.keywords" clearable @clear="audit('2')" v-else>
              <el-button slot="append" icon="el-icon-search" @click="audit('2')"></el-button>
            </el-input>
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="first">
          <el-table :data="unauditedData" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="100"></el-table-column>
            <el-table-column prop="indent" label=""></el-table-column>
            <el-table-column prop="developer_id" label="ID"></el-table-column>
            <el-table-column prop="money" label=""></el-table-column>
            <el-table-column prop="withdraw_account" label="">
              <template slot-scope="scope">
                <el-tag v-if="scope.row.withdraw_account === '0'"></el-tag>
                <el-tag type="success" v-else-if="scope.row.withdraw_account === '1'"></el-tag>
                <el-tag type="danger" v-else>QQ</el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="status" label="">
              <template slot-scope="scope">
                <el-tag type="info" v-if="scope.row.status === '0'"></el-tag>
                <el-tooltip placement="top" v-if="scope.row.status === '2'">
                  <div slot="content">{{ scope.row.cause }}</div>
                  <el-tag type="danger"></el-tag>
                </el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" label="">
              <template slot-scope="scope">
                {{ scope.row.create_time | date }}
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-tooltip :enterable="false" effect="dark" content="" placement="top">
                  <el-button type="danger" icon="el-icon-remove-outline" size="mini" @click="reject(scope.row.id)"></el-button>
                </el-tooltip>
                <el-tooltip :enterable="false" effect="dark" content="" placement="top">
                  <el-button type="success" icon="el-icon-circle-plus-outline" size="mini" @click="auditcheck(scope.row.id)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>

          <!--  -->
          <el-pagination
            @size-change="unauditedHandleSizeChange"
            @current-change="unauditedHandleCurrentChange"
            :current-page="unauditedQueryInfo.current_page"
            :page-sizes="[25, 30, 40, 50]"
            :page-size="unauditedQueryInfo.per_page"
            layout="total, sizes, prev, pager, next, jumper"
            :total="unauditedQueryInfo.total"
          ></el-pagination>

          <!--  -->
          <el-dialog title="" :visible.sync="auditDialogVisible" width="50%" @close="auditFormClose">
            <el-form :model="auditForm" ref="auditFormRef" label-width="120px">
              <el-form-item label=":">
                <span>{{ auditForm.indent }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.user }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.create_time | date }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.apply_money }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="auditForm.withdraw_account === '0'"></span>
                <span v-else-if="auditForm.withdraw_account === '1'"></span>
                <span v-else>QQ</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="auditForm.status === '0'"></span>
                <span v-else-if="auditForm.status === '1'"></span>
                <span v-else></span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.money }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="auditForm.level === '0'"></span>
                <span v-else-if="auditForm.level === '1'"></span>
                <span v-else></span>
              </el-form-item>
              <el-form-item label="(%):">
                <span>{{ auditForm.brokerage }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.mobile }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.email }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.alipay }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.alipay_name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.wxpay }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ auditForm.wxpay_name }}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ auditForm.qqpay }}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ auditForm.qqpay_name }}</span>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="auditDialogVisible = false"> </el-button>
              <el-button v-loading.fullscreen.lock="fullscreenLoading" type="primary" @click="pass"> </el-button>
            </span>
          </el-dialog>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label="" name="second">
          <el-table :data="auditData" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="100"></el-table-column>
            <el-table-column prop="indent" label=""></el-table-column>
            <el-table-column prop="developer_id" label="ID"></el-table-column>
            <el-table-column prop="money" label=""></el-table-column>
            <el-table-column prop="withdraw_account" label="">
              <template slot-scope="scope">
                <el-tag v-if="scope.row.withdraw_account === '0'"></el-tag>
                <el-tag type="success" v-else-if="scope.row.withdraw_account === '1'"></el-tag>
                <el-tag type="danger" v-else>QQ</el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="status" label="">
              <template>
                <el-tag type="success"></el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" label="">
              <template slot-scope="scope">
                {{ scope.row.create_time | date }}
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-tooltip :enterable="false" effect="dark" content="" placement="top">
                  <el-button type="success" icon="el-icon-zoom-in" size="mini" @click="checkInfo(scope.row.id)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>

          <!--  -->
          <el-pagination
            @size-change="auditHandleSizeChange"
            @current-change="auditHandleCurrentChange"
            :current-page="auditQueryInfo.current_page"
            :page-sizes="[25, 30, 40, 50]"
            :page-size="auditQueryInfo.per_page"
            layout="total, sizes, prev, pager, next, jumper"
            :total="auditQueryInfo.total"
          ></el-pagination>

          <!--  -->
          <el-dialog title="" :visible.sync="checkInfoDialogVisible" width="50%" @close="checkInfoFormClose">
            <el-form :model="checkInfoForm" ref="checkInfoFormRef" label-width="120px">
              <el-form-item label=":">
                <span>{{ checkInfoForm.indent }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.user }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.create_time | date }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.apply_money }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="checkInfoForm.withdraw_account === '0'"></span>
                <span v-else-if="checkInfoForm.withdraw_account === '1'"></span>
                <span v-else>QQ</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="checkInfoForm.status === '0'"></span>
                <span v-else-if="checkInfoForm.status === '1'"></span>
                <span v-else></span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.money }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-if="checkInfoForm.level === '0'"></span>
                <span v-else-if="checkInfoForm.level === '1'"></span>
                <span v-else></span>
              </el-form-item>
              <el-form-item label="(%):">
                <span>{{ checkInfoForm.brokerage }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.mobile }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.email }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.alipay }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.alipay_name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.wxpay }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ checkInfoForm.wxpay_name }}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ checkInfoForm.qqpay }}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ checkInfoForm.qqpay_name }}</span>
              </el-form-item>
            </el-form>
          </el-dialog>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      activeName: 'first',
      // 
      unauditedQueryInfo: {
        //
        keywords: '',
        //
        current_page: 1,
        //
        per_page: 25,
        // 
        total: 0,
        // 
        type: '1'
      },
      // 
      unauditedData: [],
      // 
      auditQueryInfo: {
        //
        keywords: '',
        //
        current_page: 1,
        //
        per_page: 25,
        // 
        total: 0,
        // 
        type: '2'
      },
      // 
      auditData: [],
      // 
      auditDialogVisible: false,
      // 
      auditForm: {},
      // 
      fullscreenLoading: false,
      // 
      checkInfoForm: {},
      // 
      checkInfoDialogVisible: false
    };
  },
  created() {
    this.audit('1');
    // 
    var self = this;
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key === 13) {
        if (self.activeName === 'first') {
          self.audit('1');
        } else {
          self.audit('2');
        }
      }
    };
  },
  methods: {
    /**
     * 
     * @param {Object} type 
     */
    async audit(type) {
      if (type === '1') {
        // 
        const { data: res } = await this.$http.get('withdraw/list', { params: this.unauditedQueryInfo });
        if (res.code !== 200) return this.$message.error(res.msg);
        this.unauditedQueryInfo.total = res.data.total;
        this.unauditedData = res.data.data;
      } else {
        // 
        const { data: res } = await this.$http.get('withdraw/list', { params: this.auditQueryInfo });
        if (res.code !== 200) return this.$message.error(res.msg);
        this.auditQueryInfo.total = res.data.total;
        this.auditData = res.data.data;
      }
    },
    /**
     * 
     */
    handleClick() {
      if (this.activeName === 'first') {
        this.audit('1');
      } else {
        this.audit('2');
      }
    },

    /**
     * 
     * @param {Object} id ID
     */
    async auditcheck(id) {
      const { data: res } = await this.$http.get(`withdraw/query/${id}`);
      if (res.code !== 200) return this.$message.error(res.msg);
      this.auditForm = res.data;
      // 
      this.auditDialogVisible = true;
    },

    /**
     * 
     */
    async pass() {
      // 
      this.auditDialogVisible = false;
      // 
      this.fullscreenLoading = true;
      const { data: res } = await this.$http.put('withdraw/pass', { id: this.auditForm.id });
      // 
      this.fullscreenLoading = false;
      if (res.code !== 200) return this.$message.error(res.msg);
      this.$message.success(res.msg);
      // 
      this.audit('1');
    },

    /**
     * 
     * @param {Object} id ID
     */
    reject(id) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async ({ value }) => {
          // 
          const { data: res } = await this.$http.put('withdraw/reject', { id: id, cause: value });
          if (res.code !== 200) return this.$message.error(res.msg);
          this.$message.success(res.msg);
          // 
          this.audit('1');
        })
        .catch(() => {
          this.$message.info('');
        });
    },

    /**
     * 
     * @param {Object} val
     */
    unauditedHandleSizeChange(val) {
      this.unauditedQueryInfo.per_page = val;
      // 
      this.audit('1');
    },

    /**
     * 
     * @param {Object} val
     */
    unauditedHandleCurrentChange(val) {
      this.unauditedQueryInfo.current_page = val;
      // 
      this.audit('1');
    },

    /**
     * 
     * @param {Object} val
     */
    auditHandleSizeChange(val) {
      this.auditQueryInfo.per_page = val;
      // 
      this.audit('2');
    },

    /**
     * 
     * @param {Object} val
     */
    auditHandleCurrentChange(val) {
      this.auditQueryInfo.current_page = val;
      // 
      this.audit('2');
    },

    /**
     * 
     * @param {Object} id ID
     */
    async checkInfo(id) {
      const { data: res } = await this.$http.get(`withdraw/query/${id}`);
      if (res.code !== 200) return this.$message.error(res.msg);
      this.checkInfoForm = res.data;
      // 
      this.checkInfoDialogVisible = true;
    },

    /**
     * 
     */
    auditFormClose() {
      // 
      this.$refs.auditFormRef.resetFields();
    },

    /**
     * 
     */
    checkInfoFormClose() {
      // 
      this.$refs.checkInfoFormRef.resetFields();
    }
  }
};
</script>

<style lang="less" scoped></style>
